<template>
    <div>
        <div class="blance-box">
            <el-row>
                <el-col :span="4" class="blance-item">
                    <p class="grid-content">{{formatterMoney(user.totalmoney)}} 元</p>
                    <p>总营收</p>
                </el-col>
                <el-col :span="5" class="blance-item">
                    <p class="grid-content">{{formatterMoney(user.income)}} 元</p>
                    <p>今日收入</p>
                </el-col>
                <el-col :span="5" class="blance-item">
                    <p class="grid-content">{{formatterMoney(user.highmoney)}} 元</p>
                    <p>历史新高</p>
                </el-col>
                <el-col :span="5" class="blance-item">
                    <p class="grid-content">{{formatterMoney(user.expenditure)}} 元</p>
                    <p>今日结算支出</p>
                </el-col>
                <el-col :span="5" class="blance-item">
                    <p class="grid-content">{{formatterMoney(user.poolmoney)}} 元</p>
                    <p>押金池</p>
                </el-col>
            </el-row>
        </div>
        <el-row class="search-bar">
            <el-col>
                <div class="lf">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20 lf">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table v-loading="listLoading" class="table" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="plat.orderNumber" label="订单号">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.orderStatus" label="订单状态" :formatter="formatOrderStatus">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.userName" label="租客">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.ownerName" label="车东">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.carBrand" label="车款">
                    </el-table-column>
                    <el-table-column align="center" label="用车时间">
                        <template scope="scope">
                            {{getTime(scope.row.plat && scope.row.plat.takeCarTime && scope.row.plat.takeCarTime.time)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="付款时间">
                        <template scope="scope">
                            {{getTime(scope.row.plat && scope.row.plat.payTime && scope.row.plat.payTime.time)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="plat.payType" label="付款渠道">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.totalPrice" label="付款金额">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.deposit" label="押金金额">
                    </el-table-column>
                    <el-table-column align="center" prop="plat.city" label="城市">
                    </el-table-column>
                    <el-table-column align="center" label="订单链接">
                        <template scope="scope">
                            <el-button type="text" @click="goOrderPage(scope.row.plat && scope.row.plat.orderNumber || '')">查看</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="时间" width="110" sortable :formatter="formatTime">
                    </el-table-column>
                    <!-- <el-table-column align="center" label="操作" width="100">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看详情</el-button>
                        </template>
                    </el-table-column> -->
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col>交易编码：<span>{{form.financeRecordUuid}}</span></el-col>
                    <el-col :span="12">转出人：<span>{{form.outMan}}</span></el-col>
                    <el-col :span="12">转出账户：<span>{{form.outUser}}</span></el-col>
                    <el-col :span="12">转入人：<span>{{form.intoMan}}</span></el-col>
                    <el-col :span="12">转入账户：<span>{{form.intoUser}}</span></el-col>
                    <el-col>交易金额：<span>{{form.money}}</span></el-col>
                    <el-col>收支类型：<span>{{formatFinanceType(form)}}</span></el-col>
                    <el-col>交易内容：<span>{{formatOrderRecordType(form)}}</span></el-col>
                    <el-col>交易时间：<span>{{formatTime(form)}}</span></el-col>
                </el-row>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            editDialog: false, //弹出框-编辑
            form: {},
            user: {
                balance: 0,
                income: 0,
                expenditure: 0,
            },
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        tableMaxHeight() {
            // return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    methods: {
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatFinanceType(row, column) {
                return row.financeType == 1 ? '收入' : row.financeType == 2 ? '支出' : '未知';
            },
            // 格式化状态
            formatterMoney(val) {
                return this.$common.formatterMoney(val)
            },
            // 格式化状态
            formatOrderRecordType(row, column) {
                switch (row.financeType) {
                    case 1: return '押金'
                    case 2: return '租车费用'
                    case 3: return '赔偿费用'
                    case 4: return '订单提成'
                    case 5: return '保险费用'
                    default: return '未知'
                }
            },
            getTime(time) {
                return this.$common.getNowTime(time)
            },
            formatOrderStatus(row, column) {
                if (!row.plat) {
                    return '--'
                }
                if (row.plat.depositStatus == 2) {
                    switch(row.plat.orderStatus) {
                        case 3: return '预定成功'
                        case 4: return '服务中'
                        case 5: return '服务中'
                        case 6: return '已还车'
                        default: return '--'
                    }
                } else if (row.plat.depositStatus == 3){
                    return '以结案'
                } else {
                    return '--'
                }
            },
        // 查看
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "financeRecordUuid": row.financeRecordUuid
                }
            }
            this.$common._post('platform/detail', json, this, data => {
                this.editDialog = true
                this.form= Object.assign({}, data.financeRecord)
            })
        },
        // 跳转至订单
        goOrderPage(orderNumber) {
            this.$router.push('/order')
            this.$store.commit('setOrderNumber', orderNumber)
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 导出
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelPlatform', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        // 获取列表
        getList(){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('platform/list', json, this, data => {
                this.total = data.total
                this.tableData = data.platformList

                this.user = data

                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



